import { CoreModule } from '../../core/core.module';
import { routing } from './usertrainer.routing';
import { SlickModule } from 'ngx-slick';
import { CoreModuleNew } from 'newcore/new-core.module';
@NgModule({
  imports: [
    CommonModule,
    InfiniteScrollModule,
    CoreModule,
    routing,
    SlickModule.forRoot(),
    CoreModuleNew
  ],
  declarations: [
    UserstrainermoduleComponent
  ]
})
export class UserTrainerModule { }



