import { MixpanelService } from './../../core/mixpanelService.service';
import { UserSession } from './../../core/sessions';
import { GymBranchDistinct, TrainerBranchResponseList } from './../../core/models/trainerDetailResponse';
import { HttpCommonUtilsService } from '../../core/http_wrapper/http-common-utils.service';
import { GymList } from './../../core/models/gymlist';
import { Router } from '@angular/router';
import { BSModalContext, Modal } from 'angular2-modal/plugins/bootstrap';
import { overlayConfigFactory } from 'angular2-modal';
import { ModelpopupLrComponent } from '../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { LocalStorageService } from './../../core/service/local-storage.service';
import { AllTrainerResponseDetail } from './../../core/models/alltrainerResponse';
import { element } from 'protractor';
import { HttpSuccesFailureResponse } from './../../core/http_wrapper/http_wrapper_response';
import { HttpUserAuthUtilsService } from '../../core/http_wrapper/http-userauth-utils.service';
import { Component, OnInit } from '@angular/core';
import { Constants, InfiniteScroll, MixpanelEvent, SeoElements } from '../../core/constants';
import { AllTrainerResponse } from '../../core/models/alltrainerResponse';
import { ErrorpopupmodelComponent } from '../../common/errorpopupmodel/errorpopupmodel.component';
import { ActivitiesListData } from '../../core/models/activitiesListResponse';
import { Meta } from '@angular/platform-browser';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';




declare var $: any;
declare var jQuery: any;
declare var _default: any;
declare var settings: any;


@Component({
  selector: 'app-userstrainermodule',
  templateUrl: './userstrainermodule.component.html',
  styleUrls: ['./userstrainermodule.component.css']
})
export class UserstrainermoduleComponent implements OnInit, HttpSuccesFailureResponse {
  allTrainerList: AllTrainerResponseDetail[] = [];
  selectedTrainerId: number;
  selectedTrainerName: string;
  activitiesListDataResponse: ActivitiesListData[] = [];
  catagoryId = 0;
  currentPage = 0;
  isLoadMoreData = true;
  lastScrollDistance = 0;
  TarinerListisLaoding = false;
  temp = 0;
  isLoaded = false;
  slideConfig: any;
  public throttle = InfiniteScroll.throttle;
  public scrollDistance = InfiniteScroll.scrollDistance;
  public scrollUpDistance = InfiniteScroll.scrollUpDistance;



  constructor(private globalSubscriptionService: GlobalSubscriptionService,private http_user: HttpUserAuthUtilsService, private http_common: HttpCommonUtilsService,
    private modal: Modal, private mixpanel: MixpanelService, private router: Router, private meta: Meta) {
    this.meta.updateTag({ name: SeoElements.title, content: SeoElements.userTrainerMetaTitle })
    this.meta.updateTag({ name: SeoElements.description, content: SeoElements.userTrainerDescription })
    this.meta.updateTag({ name: SeoElements.keywords, content: SeoElements.userTrainerKeywords })

  }


  ngOnInit() {
    this.getActivitiesList();
    this.TarinerListisLaoding = true;
    this.trainerList(this.catagoryId, true);
    this.slideConfig = {
      slidesToShow: 8,
      slidesToScroll: 1,
      infinite: false,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 6,
            slidesToScroll: 1,
            infinite: true,
            dots: false
          }
        },
        {
          breakpoint: 767,
          settings: {
            slidesToShow: 6,
            slidesToScroll: 1,
            dots: false

          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 1,
            dots: false

          }
        }
      ]
    };
    this.globalSubscriptionService.StoreUpdatedState.subscribe((value) => {
      this.ngOnInit();
    });
  }
  getActivitiesList() {

    this.http_common.getAllActivity(this, false, 1);
  }
  trainerList(categoryid, showLoader) {
    this.temp = categoryid;
    this.TarinerListisLaoding = true;
    if (showLoader) {
      this.resetBeforeLoad();

    }
    this.catagoryId = categoryid;
    this.http_user.getAllTrainerList(this.catagoryId, this.currentPage, this, showLoader);
  }

  onSuccess(type: any, responsedata: any) {
    if (type === Constants.AllTrainerApi) {
      this.TarinerListisLaoding = false;
      const temp_list: AllTrainerResponseDetail[] = responsedata.data;
      temp_list.forEach(element => {
        this.allTrainerList.push(element);
      });
      const trainerListKeywords = []
      this.allTrainerList.forEach(element => {
        trainerListKeywords.push(element.firstName)
      });
      this.allTrainerList.forEach(element => {
        element.profileImageThumbnail = element.profileImageThumbnail;
        element.categoryListStr = element.categoryList.join(', ').toString();
        try {
          element.gymBranchDistinct = this.setupGyminfoOfTrainer(element.trainerBranchResponseList);

        } catch (e) {
          element.gymBranchDistinct = [];

        }

      });
      if (temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;

      }

    }
    if (type === Constants.MarkFavouriteTrainer) {
      this.mixpanel.logEventWithAnalytics(MixpanelEvent.addFavorite, {
        'origin': 'Trainer_Tab', 'Source': 'web',
        'Trainer_Name': this.selectedTrainerName
      })
      this.allTrainerList.forEach(element => {
        if (element.id == this.selectedTrainerId) {
          element.isfollow = true;

        }
      });
    }
    if (type === Constants.MarkTrainerAsUnFavrourite) {
      this.mixpanel.logEventWithAnalytics(MixpanelEvent.removeFromFavourite, {
        'origin': 'Trainer_Tab', 'Source': 'web',
        'Trainer_Name': this.selectedTrainerName
      });
      this.allTrainerList.forEach(element => {
        if (element.id === this.selectedTrainerId) {
          element.isfollow = false;
        }
      });

    }

    if (type == Constants.AllActivityListApi) {
      this.activitiesListDataResponse = UserSession.setStatusForList(responsedata.data);

    }

  }
  onFailure(type: any, response: string) {
    if (type == Constants.AllTrainerApi) {
    }
    if (type == Constants.MarkFavouriteTrainer) {
      this.errorPopupModalMessage('Failed to mark Favourite', response);
    }
    if (type == Constants.MarkTrainerAsUnFavrourite) {
      this.errorPopupModalMessage('Failed to mark UnFavourite', response);

    }

  }
  gymlocationnavigate(value: GymBranchDistinct) {
    sessionStorage.setItem('gym_id', value.gymId + '');
    this.mixpanel.logEventWithAnalytics(MixpanelEvent.visitGymProfile, { 'origin': 'Trainer_Tab', 'Source': 'web' });
    this.router.navigate(['gym', value.gymId, value.gymName.replace(/ /g, '')]);

  }

  markTrainerAsFavourite(value: AllTrainerResponseDetail) {
    this.selectedTrainerId = value.id;
    this.selectedTrainerName = value.firstName;
    if (LocalStorageService.getIsLoggedIn()) {
      if (value.isfollow) {
        this.http_user.markTrainerAsUnFavourit(value.id, LocalStorageService.getUserId(), this, true);
      } else {
        this.http_user.markTrainerAsFavourit(value.id, LocalStorageService.getUserId(), this, true);
      }
    } else {
      return this.modal.open(ModelpopupLrComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));
    }

  }
  errorPopupModalMessage(status, message) {
    return this.modal.open(ErrorpopupmodelComponent, overlayConfigFactory({ status, message }, BSModalContext));
  }
  routeToTrainerDetails(value: AllTrainerResponseDetail) {
    sessionStorage.setItem('trainer_id', value.id + '');
    this.mixpanel.logEventWithAnalytics(MixpanelEvent.visitTrainerProfile, { 'origin': 'Trainer_Tab', 'Source': 'web' });
    this.router.navigate(['/trainer', value.id, value.firstName.replace(/ /g, '')]);
  }
  onScrollDown() {
    if (this.isLoadMoreData && !this.TarinerListisLaoding) {
      this.currentPage = this.currentPage + 1;
      this.trainerList(this.catagoryId, false);

    }
  }
  onScroll() {

  }
  resetBeforeLoad() {
    this.currentPage = 0;
    this.isLoadMoreData = true;
    this.allTrainerList = [];
    this.lastScrollDistance = 0;
  }


  setupGyminfoOfTrainer(associatedBranchList: TrainerBranchResponseList[]) {
    const gymListMap: Map<number, GymBranchDistinct[]> = new Map<number, GymBranchDistinct[]>();

    const gymBranchDistinct: GymBranchDistinct[] = [];

    const distinctgymid: number[] = [];
    associatedBranchList.forEach(element => {
      if (distinctgymid.indexOf(element.gymId) == -1) {
        distinctgymid.push(element.gymId);
      }
    });
    distinctgymid.forEach(element => {
      const myarray: GymBranchDistinct[] = [];
      for (let i = 0; i < associatedBranchList.length; i++) {
        if (element == associatedBranchList[i].gymId) {
          myarray.push({ 'gymName': associatedBranchList[i].gymName, 'gymId': associatedBranchList[i].gymId, 'localitybranchName': associatedBranchList[i].localityName });
        }
      }
      gymListMap.set(distinctgymid.indexOf(element) + 1, myarray);
    });

    gymListMap.forEach(element => {
      const gymDetails: string[] = [];
      element.forEach(element => {
        gymDetails.push(element.localitybranchName);
      });

      gymBranchDistinct.push({ 'gymName': element[0].gymName, 'gymId': element[0].gymId, 'localitybranchName': gymDetails.join(' ,') })
    });
    return gymBranchDistinct;
  }
}



